import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '*',
      redirect: '/Auth'
    },
    {
      path: '/Auth',
      name: 'Auth',
      component: () => import('@/views/Auth.vue')
    },
    {
      path: '/Register',
      name: 'Register',
      component: () => import('@/views/Register.vue')
    },
    {
      path: '/Weather',
      name: 'Weather',
      component: () => import('@/views/Weather.vue')
    }
  ]
})
